import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import image1 from "src/assets/images/art1.jpg";
import image2 from "src/assets/images/art2.jpg";
import image3 from "src/assets/images/art3.jpg";
import image4 from "src/assets/images/art4.jpg";
import SwiperCore, { Navigation } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper-bundle.css";

SwiperCore.use([Navigation]);

const useStyles = makeStyles((theme) => ({
  sliderRoot: {
    height: 600,
  },
  imageStyle: {
    height: 600,
    width: "100%",
    objectFit: "cover",
  },
  mySwiper: {},
}));

const ImageSlider = (props: any) => {
  const classes = useStyles();

  const params: any = {
    pagination: {
      el: ".swiper-pagination",
      type: "bullets",
      clickable: true,
    },
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    spaceBetween: 30,
  };

  return (
    <div className={classes.sliderRoot}>
      <Swiper {...params} className={classes.mySwiper}>
        <SwiperSlide>
          <img src={image2} alt="" className={classes.imageStyle} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={image3} alt="" className={classes.imageStyle} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={image4} alt="" className={classes.imageStyle} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={image1} alt="" className={classes.imageStyle} />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default ImageSlider;
